.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.icon {
  width: 40px;
  height: 40px;
  display: block;
}

.image__icon {
  width: 100%;
  height: 100%;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  margin-left: 20px;
}

.header__nav-item:first-child {
  margin-left: 0;
}

.header__nav-link {
  text-transform: uppercase;
  white-space: nowrap;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header__nav-link.is-active {
  color: #00acdc;
}

.header__nav-link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.header__nav-link:hover {
  color: #00acdc;
}
